import React, {Component} from 'react';
import './BuildSummary.css';

class BuildSummary extends Component {
  render() {
    return (
        <div className="build-summary-container">
            <div className="build-melee-summary">
                <div className="weapon-icon sienna-flail border-04"></div>
                <div className="trait-icon trait-swift-slaying border-04"></div>
                <div className="property-container">
                    <li className="item-property-1">+ 30.0% Push/Block Angle</li>
                    <li className="item-property-2">+ 5.0% Crit Chance</li>
                </div>
            </div>
            <div className="build-range-summary">
                <div className="weapon-icon sienna-conflag border-04"></div>
                <div className="trait-icon trait-barrage border-04"></div>
                <div className="property-container">                                                    
                    <li className="item-property-1">+ 30.0% Push/Block Angle</li>
                    <li className="item-property-2">+ 5.0% Crit Chance</li>
                </div>
            </div>
            <div className="build-jewelry-summary">
                <div className="jewelry-icon necklace-icon border-04"></div>
                <div className="trait-icon trait-barkskin border-04"></div>
                <div className="necklace-property-container">                                    
                    <li className="item-property-1">+ 30.0% Push/Block Angle</li>
                    <li className="item-property-2">+ 5.0% Crit Chance</li>
                </div>
                <div className="jewelry-icon charm-icon border-04"></div>
                <div className="trait-icon trait-proxy border-04"></div>
                <div className="charm-property-container">                                    
                    <li className="item-property-1">+ 30.0% Push/Block Angle</li>
                    <li className="item-property-2">+ 5.0% Crit Chance</li>
                </div>
                <div className="jewelry-icon trinket-icon border-04"></div>
                <div className="trait-icon trait-shrapnel border-04"></div>
                <div className="trinket-property-container">
                    <li className="item-property-1">+ 30.0% Push/Block Angle</li>
                    <li className="item-property-2">+ 5.0% Crit Chance</li>
                </div>
            </div>
        </div>
        );
      }
    }
    
    export default BuildSummary;