import React, {Component} from 'react';
import './Inventory.css';
import WeaponSelect from '../weaponSelect/WeaponSelect'
import InventoryItemDisplay from './InventoryItemDisplay'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import "react-tabs/style/react-tabs.css";

class Inventory extends Component {
  render() {

    console.log('rendering  ');
    return (
        <Tabs className="container-tabs inventory-container">
            <TabList className="container-tabs-list">
                <Tab>Melee</Tab>
                <Tab>Range</Tab>
                <Tab>Jewelry</Tab>
            </TabList>
            <TabPanel>
                <WeaponSelect type={"melee"}></WeaponSelect>
                <InventoryItemDisplay type={"melee"}></InventoryItemDisplay>
            </TabPanel>
            <TabPanel>
                <WeaponSelect type={"range"}></WeaponSelect>
                <InventoryItemDisplay type={"range"}></InventoryItemDisplay>
            </TabPanel>
            <TabPanel className="jewelry-tab react-tabs__tab-panel">
                <InventoryItemDisplay type={"jewelry"}></InventoryItemDisplay>
                <InventoryItemDisplay type={"jewelry"}></InventoryItemDisplay>
                <InventoryItemDisplay type={"jewelry"}></InventoryItemDisplay>
            </TabPanel>
        </Tabs>
    );
  }

  renderWeapons() {
    if (this.props.weaponType === 'melee') {
        return (
            <div class="weapon-container">
                <div className="weapon-icon sienna-sword"></div>
                <div className="weapon-icon sienna-firesword"></div>
                <div className="weapon-icon sienna-dagger"></div>
                <div className="weapon-icon sienna-mace"></div>
                <div className="weapon-icon sienna-crowbill selected"></div>
                <div className="weapon-icon sienna-flail"></div>
            </div>            
        );
    } else {
        return (            
        <div class="weapon-container">
            <div className="weapon-icon sienna-fireball"></div>
            <div className="weapon-icon sienna-conflag selected"></div>
            <div className="weapon-icon sienna-beam"></div>
            <div className="weapon-icon sienna-bolt"></div>
            <div className="weapon-icon sienna-flamestorm"></div>
        </div>
        );
    }
  }
}

export default Inventory;