import React, {Component} from 'react';
import './HeroTalents.css';
import { AppContext } from '../../stores/Store';
import { withRouter } from "react-router";
import {heroesData} from '../../data/Heroes'

class HeroTalents extends Component {
  static contextType = AppContext;

  constructor(props) {
    super(props);
    this.renderTalents = this.renderTalents.bind(this);
    this.renderTalent = this.renderTalent.bind(this);
  }

  renderTalents() {
    const [state, updateState] = this.context;

    console.log('rendering talents');

    let talentList = [];
    var selectedTalents = this.props.talents ? this.props.talents : [0,0,0,0,0,0];
    var careerId = this.props.careerId;

     for (var i=1; i<19; i++) {
       var talentString = `talent-${careerId.toString().padStart(2, "0")}${i.toString().padStart(2, "0")}`;
       var tier = Math.floor((i-1) / 3);
       var talentClassName = `hero-talent border-01 tier-${tier+1} ${talentString}`;

       (selectedTalents[tier] + (tier*3) ) == i ? talentClassName += " selected" : talentClassName = talentClassName;

       var tierString = `level-${(tier+1)*5}`

       if (i%3 == 1) {
        talentList.push(<div key={tierString} className={`talent-lock-icon ${tierString}`}></div>);
       }

       var talentValue = i % 3 == 0 ? 3 : i % 3;
       

      talentList.push(this.renderTalent(careerId, i, selectedTalents));
     }
     return talentList;
  }

  renderTalent(careerId, talentNumber, selectedTalents) {
    const [state, updateState] = this.context;
    var talentString = `talent-${careerId.toString().padStart(2, "0")}${talentNumber.toString().padStart(2, "0")}`;
    var tier = Math.floor((talentNumber-1) / 3);
    var talentClassName = `hero-talent tier-${tier+1}`;

    (selectedTalents[tier] + (tier*3) ) == talentNumber ? talentClassName += " selected" : talentClassName = talentClassName;

    var talentValue = talentNumber % 3 == 0 ? 3 : talentNumber % 3;
    var talent = heroesData[careerId-1].talents[talentNumber-1];

    return <button data-talent={talentValue} data-tier={tier} onClick={(e) => updateState({type: "UPDATE_TALENTS", payload: { tier: parseInt(e.currentTarget.dataset.tier), talent: parseInt(e.currentTarget.dataset.talent)}})} key={talentString} className={talentClassName}>
            <div className="talent-button-wrapper">
              <div className={`talent-icon ${talentString}`}></div>
              <p className="talent-name">{talent.name}</p>
              <p className="talent-description">{talent.description}</p>
            </div>
           </button>;
  }

  render() {
    return (
      <div className="hero-talents-container">
        <div className="hero-talents-grid">
          <p className="hero-talents-header">Talents</p>          
          {this.renderTalents()}
        </div>
      </div>
    );
  }

  handleTalentClick(e) {
    var talent = e;
    console.log(e);
    console.log(e.currentTarget.getAttribute('data-tier'));
    var tierTalents = document.querySelectorAll(`button.hero-talent[data-tier="${e.currentTarget.getAttribute('data-tier')}"]`);

    tierTalents.forEach(talent => {
      talent.classList.remove('selected');
    });

    e.currentTarget.classList.add('selected');
  }
}

export default withRouter(HeroTalents);
