import React, {Component} from 'react';
import './HeroSelect.css';
import { AppContext } from '../../stores/Store';
import {withRouter} from 'react-router';

class HeroSelect extends Component {
  constructor(props) {
    super(props);
    this.renderHeroes = this.renderHeroes.bind(this);
    this.updateHeroSelect = this.updateHeroSelect.bind(this);
  }

  static contextType = AppContext;

  render() {
    console.log('render hero select');
    
    return (
      <div className="hero-select-container background-12">
        <div className="hero-select-header label-05 border-01">
          <div className="hero-select-header-background">
            <p>Hero Selection</p>
          </div>
        </div>
        <div className="hero-icon-container background-12 border-01">
        {this.renderHeroes()}
        </div>
      </div>
    );
  }

  renderHeroes() {
    var careerId = this.props.careerId;

    let heroIconList = [];
    for (var i=1;i<16;i++) {
      let heroClassName = "border-02 hero-icon-0" + i;
      let keyValue = "hero-icon-0" + i;
      if (i>9){
        heroClassName = "border-02 hero-icon-" + i;
        keyValue = "hero-icon-" + i;
      }
      if (i == careerId) {
        heroClassName += " selected";
      }

      heroIconList.push(<button data-career={i} onClick={this.updateHeroSelect}
                           key={keyValue} className={heroClassName}></button>);
    }
    return heroIconList;
  }

  routeCareer(careerId) {
    let path = `/heroes/${careerId}`;
    this.props.history.push(path);
  }

  updateHeroSelect(e) {
    console.log('hero select state');
    const [state, updateState] = this.context;
    updateState({type: "UPDATE_CAREER", payload: { careerId: e.target.dataset.career, history: this.props.history}})
  }
}

export default withRouter(HeroSelect);
