import React, {Component} from 'react';
import './InventoryItemDisplay.css';

class InventoryItemDisplay extends Component {
    render() {
      return (
          <div className="inventory-item-display-container">
              <p className="inventory-item-header">Flaming Flail</p>
              <div className="inventory-item-summary-container">
                  <p className="item-power-level">300</p>
                  <p className="item-stamina stamina-90">3</p>
                  <div className="item-properties-container">
                    <li className="item-property-1">+ 30.0% Push/Block Angle</li>
                    <li className="item-property-2">+ 5.0% Crit Chance</li>
                  </div>
                  <div className="item-trait-container">
                    <div className="item-trait-icon trait-icon trait-resourceful-combatant"></div>
                    <p className="item-trait-name">Resourceful Combatant</p>
                    <p className="item-trait-description">Melee critical hits reduce the cooldown of your Career Skill by 5.0%. This effect can only trigger every 4 seconds.</p>
                  </div>
              </div>
              <p className="inventory-item-footer">Ignore Shields, Wide Sweeps, Crowd Control</p>
          </div>
      );
    }
}

export default InventoryItemDisplay;