import React from 'react'

const AppContext = React.createContext();

export {AppContext}

function reducer(state, action) {
    console.log('reducer');
    console.log(this);

    switch(action.type) {
        case 'UPDATE_BUILDS_CAREER':
            var buildsPage = {...state.buildsPage};
            buildsPage.careerId = action.payload.careerId;
            return {...state, buildsPage};
        case 'UPDATE_HERO_CAREER':
            var heroPage = {...state.heroPage};
            heroPage.careerId = action.payload.careerId;
            console.log(action.payload);
            action.payload.history.push(`/heroes/${action.payload.careerId}`);
            return {...state, heroPage};
        case 'UPDATE_HERO_TALENTS':
            var heroPage = {...state.heroPage};
            console.log(heroPage.talents);
            console.log(action.payload);
            console.log('tier ' + action.payload.tier + ' talent ' + action.payload.talent)
            heroPage.talents[action.payload.tier] = action.payload.talent;
            console.log(heroPage.talents);
            return {...state, heroPage};
        case 'UPDATE_BUILD_TALENTS':
            var buildPage = {...state.editBuildPage};
            console.log(buildPage.talents);
            console.log(action.payload);
            console.log('tier ' + action.payload.tier + ' talent ' + action.payload.talent)
            buildPage.talents[action.payload.tier] = action.payload.talent;
            console.log(buildPage.talents);
            return {...state, buildPage};
    }
}

