import React from 'react'
import { AppContext } from './Store';

const BuildPageContext = React.createContext();

export {BuildPageContext}

function reducer(state, action) {
    switch(action.type) {
        case 'UPDATE_CAREER':
            return {...state, careerId: action.payload};
        case 'UPDATE_TALENTS':
            var newTalents = {...state.talents};
            newTalents[action.payload.tier] = action.payload.talent;
            console.log(state.talents);
            console.log(newTalents);
            return {...state, talents: newTalents};
    }
}

export default function BuildPageStore(props) {

    const stateHook = React.useReducer(reducer, {
        careerId: 1,
        talents: [1,5,8,10,15,18],
        melee: {
            id: 1,
            property1Id: 1,
            property1Value: 10,
            property2Id: 2,
            property2Value: 10,
            traitId: 1
        },
        range: {
            id: 1,
            property1Id: 1,
            property1Value: 10,
            property2Id: 2,
            property2Value: 10,
            traitId: 1
        },
        necklace: {
            id: 1,
            property1Id: 1,
            property1Value: 10,
            property2Id: 2,
            property2Value: 10,
            traitId: 1
        },
        charm: {
            id: 1,
            property1Id: 1,
            property1Value: 10,
            property2Id: 2,
            property2Value: 10,
            traitId: 1
        },
        trinket: {
            id: 1,
            property1Id: 1,
            property1Value: 10,
            property2Id: 2,
            property2Value: 10,
            traitId: 1
        }
    });

    return (
        <AppContext.Provider value={stateHook}>
            {props.children}
        </AppContext  .Provider>
    )
}