import React, {Component} from 'react';
import './WeaponSelect.css';

class WeaponSelect extends Component {
  render() {
    return (
        <div className={this.props.type + "-container inventory-item-container border-01"}>
        <p className={this.props.type + "-header"} style={{textTransform: 'uppercase'}}>{this.props.type}</p>
        <div className={this.props.type + "-divider divider-06"}></div>
        {this.renderWeapons()}
    </div>
    );
  }

  renderWeapons() {
    if (this.props.type === 'melee') {
        return (
            <div className="weapon-container">
                <div className="weapon-icon sienna-sword"></div>
                <div className="weapon-icon sienna-firesword"></div>
                <div className="weapon-icon sienna-dagger"></div>
                <div className="weapon-icon sienna-mace"></div>
                <div className="weapon-icon sienna-crowbill selected"></div>
                <div className="weapon-icon sienna-flail"></div>
            </div>            
        );
    } else {
        return (            
        <div className="weapon-container">
            <div className="weapon-icon sienna-fireball"></div>
            <div className="weapon-icon sienna-conflag selected"></div>
            <div className="weapon-icon sienna-beam"></div>
            <div className="weapon-icon sienna-bolt"></div>
            <div className="weapon-icon sienna-flamestorm"></div>
        </div>
        );
    }
  }

  handleItemClick(e) {
    console.log(e);
    console.log(e.currentTarget);

    e.currentTarget.classList.add('selected');
  }
}

export default WeaponSelect;